.aboutSection {
  display: flex;
  flex-direction: row;
  margin: 35px 0;
}

.about {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 60%;
}

.myPhoto {
  height: 300px;
  margin: 0 40px;
  border: 2px #142d4c solid;
  -webkit-box-shadow: 8px 8px 24px 0px rgba(0, 0, 0, 1);
  -moz-box-shadow: 8px 8px 24px 0px rgba(0, 0, 0, 1);
  box-shadow: 8px 8px 24px 0px rgba(0, 0, 0, 1);
  border-radius: 200px;
}

.personalTexts {
  display: flex;
  flex-direction: column;
  width: 60%;
  padding: 0 15px;
  background-color: #274060;
  border-radius: 25px;
  border: 2px solid #142d4c;
}

.infoLinks {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 60px;
}

.github {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #ececec;
  margin: 15px;
  animation-name: longAppearRight;
  animation-duration: 0.5s;
}

.cv {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #ececec;
  margin: 15px;
  animation-name: longAppearLeft;
  animation-duration: 0.8s;
}

@keyframes longAppearRight {
  0% {
    opacity: 0%;
    margin-left: 300px;
  }
  50% {
    opacity: 0%;
  }
  100% {
    margin-left: 15px;
    opacity: 100%;
  }
}

@keyframes longAppearLeft {
  0% {
    margin-right: 300px;
    opacity: 0%;
  }
  60% {
    opacity: 0%;
  }
  100% {
    margin-right: 15px;
    opacity: 100%;
  }
}

.github:hover,
.cv:hover {
  text-decoration: underline;
}

.githubLogo {
  width: 40px;
  margin: 0 5px;
}

.cvLogo {
  width: 40px;
  margin: 0 5px;
  background-color: white;
  border-radius: 20px;
  border: 1px solid black;
}

@media (max-width: 1409px) {
  .cv {
    margin-bottom: 60px;
  }
}

@media (max-width: 1000px) {
  .about {
    flex-direction: column;
    align-items: center;
    padding: 0;
  }

  .myPhoto {
    height: 300px;
    width: fit-content;
  }

  .personalTexts {
    margin-top: 20px;
    padding: 15px;
    width: 80%;
  }
}

@media (max-width: 787px) {
  .left,
  .right {
    display: none;
  }

  h2 {
    font-size: 20px;
  }

  p {
    font-size: 18px;
  }
}

@media (max-width: 500px) {
  h2 {
    font-size: 18px;
  }

  p {
    font-size: 16px;
  }

  .github,
  .cv {
    font-size: 18px;
  }
}

@media (max-width: 400px) {
  .github,
  .cv {
    font-size: 16px;
  }
}
