.apiSectionWrapper {
  display: flex;
  flex-direction: row;
}

.apiSection {
  display: flex;
  flex-direction: column;
  width: 60%;
}

.title {
  display: flex;
  justify-self: center;
  align-self: center;
}

.dataApiSection {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.apiBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid #9fd3c7;
  background-color: #142d4c;
  padding: 15px 25px;
  border-radius: 25px;
  margin: 20px 10px;
}

@media (max-width: 900px) {
  .dataApiSection {
    flex-direction: column;
  }

  .apiBox:last-child {
    margin-bottom: 50px;
  }

  .title {
    font-size: 24px;
  }
}
